import React, { useEffect } from "react";
import { BrowserRouter, Route, Routes } from "react-router-dom";
import AboutProj from "./components/AboutProj";
import Header from "./components/Header";
import Hero from "./components/Hero";
import Home from "./components/Home";
import Intro from "./components/Intro";
import Model from "./components/Model";
import ModelsSection from "./components/ModelsSection";
import { ModelsState } from "./context/Context";

function App() {

  useEffect(() => {

    function handleScroll(){
      const elements = document.querySelectorAll(".fade-in");

      const fadeInOptions = {
        root: null,
        threshold: 0.1,
        rootMargin: '0px',
      };

      const fadeInObserver = new IntersectionObserver((entries, observer) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting) {
            if(entry.target.classList.contains('fade-300')){
              setTimeout(()=> entry.target.classList.add("fade-in-animation"), 600)
            }
            if(entry.target.classList.contains('fade-600')){
              setTimeout(()=> entry.target.classList.add("fade-in-animation"), 1000)
            }
            else{
              entry.target.classList.add("fade-in-animation");
            }
            observer.unobserve(entry.target);
          }
        });
      }, fadeInOptions);
  
      elements.forEach((element) => {
        fadeInObserver.observe(element);
      });

    }

    document.addEventListener(`scroll`, handleScroll, { passive: true });
   
    return () => {
      document.removeEventListener('scroll', handleScroll, { passive: true });
    };
  }, []);


  const { state, dispatch } = ModelsState();

  return (
    <>
    {
    // state.isFilled ?
    <div className="relative z-0 app w-full m-auto">
      <BrowserRouter>
        <Header />
        <Routes>
          <Route path="/" element={<Home />}/>
          <Route path="/models/:modelId" element={<Model />}/>
        </Routes>
          
      </BrowserRouter>
    </div>
      // :
      // <div className="popup-window flex align-middle flex-col justify-center absolute z-50 inset-0">
      //   <div className="popup-blank flex flex-col justify-center items-center gap-y-4">
      //     <div>     ?</div>
      //     <div className="select-blank flex align-middle gap-x-3">
      //       <button className="blank-button" onClick={() => dispatch({type: "WITH_MODELS", payload: state})}></button>
      //       <button className="blank-button" onClick={() => dispatch({type: "NO_MODELS", payload: state})}></button>
      //     </div>
      //   </div>
      // </div>
    } 
    </>
    
  );
}

export default App;
