import { Canvas } from '@react-three/fiber'
import { Environment, Lightformer, OrbitControls, useGLTF } from '@react-three/drei'
import { Suspense } from 'react'
import { useParams } from 'react-router-dom'
import ModelsData from './heroes/ModelsData'



 function Model() {

    const { modelId } = useParams();

    function Scene() {
        const { scene } = useGLTF(ModelsData[modelId - 1].path)
        return (
        <Suspense>
          <primitive
              receiveShadow
              object={scene}
              scale={0.4} 
              position={[0, -3, 0]}
              rotation={[0, 1.2, 0]}
          />
          </Suspense>
        )
      }

  return (
    <div className='w-full h-full canvas-div' style={{height: "100vh"}}>
    <Canvas gl={{ logarithmicDepthBuffer: true, antialias: false }} dpr={[1, 1.5]} camera={{ position: [0, 0, 15], fov: 25 }}>
      <Suspense>
      <Scene rotation={[0, Math.PI / 1.5, 0]} scale={0.015} />
      <hemisphereLight intensity={0.5} />
        <Environment resolution={512}>
          {/* Ceiling */}
          <Lightformer intensity={2} rotation-x={Math.PI / 2} position={[0, 4, -9]} scale={[10, 1, 1]} />
          <Lightformer intensity={2} rotation-x={Math.PI / 2} position={[0, 4, -6]} scale={[10, 1, 1]} />
          <Lightformer intensity={2} rotation-x={Math.PI / 2} position={[0, 4, -3]} scale={[10, 1, 1]} />
          <Lightformer intensity={2} rotation-x={Math.PI / 2} position={[0, 4, 0]} scale={[10, 1, 1]} />
          <Lightformer intensity={2} rotation-x={Math.PI / 2} position={[0, 4, 3]} scale={[10, 1, 1]} />
          <Lightformer intensity={2} rotation-x={Math.PI / 2} position={[0, 4, 6]} scale={[10, 1, 1]} />
          <Lightformer intensity={2} rotation-x={Math.PI / 2} position={[0, 4, 9]} scale={[10, 1, 1]} />
          {/* Sides */}
          <Lightformer intensity={2} rotation-y={Math.PI / 2} position={[-50, 2, 0]} scale={[100, 2, 1]} />
          <Lightformer intensity={2} rotation-y={-Math.PI / 2} position={[50, 2, 0]} scale={[100, 2, 1]} />
          {/* Key */}
          <Lightformer form="ring" color="red" intensity={10} scale={2} position={[10, 5, 10]} onUpdate={(self) => self.lookAt(0, 0, 0)} />
        </Environment>
      
      <OrbitControls
        //   enableZoom={false} 
        //   minPolarAngle={Math.PI / 2.2} 
        //   maxPolarAngle={Math.PI / 2.2} 
      />
      </Suspense>
    </Canvas>
    </div>
  )
}
  



export default Model;